/* First Section */

.Home .first_section {
    width: 100%;
    height: auto;
    position: relative;
    overflow: hidden;
}

.Home .first_section video {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.Home .first_section2 {
    text-align: center;
    font-size: 1.25rem;
    padding-top: 3.3vmax;
}

/* Second Section */

.Home .second_section_Title {
    text-align: center;
}

.Home .second_section_Title h2 {
    font-size: 2rem;
    padding-bottom: 3.3vmax;
}

.Home .Each_Section {
    display: flex;
    flex-direction: row;
    padding-bottom: 4rem;
}

.Home .Each_Section .grid-container,
.Home .Each_Section .grid-container_right {
    display: flex;
    flex-direction: row;
    position: relative;

}

.Home .Each_Section .grid-container::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 85%;
    height: 100%;
    background-color: white;
    box-shadow: -1px 1px 20px 9px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    border-radius: 10px;
    z-index: 0;
}

.Home .Each_Section .grid-container_right::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 85%;
    height: 100%;
    background-color: white;
    box-shadow: -1px 1px 20px 9px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    border-radius: 10px;
    z-index: 0;
}

.Home .Each_Section .ResearchImage,
.Home .Each_Section .ResearchImage_right {
    flex: 3;
    z-index: 1;
    display: flex;
    align-items: center;
}

.Home .Each_Section .ResearchImage img,
.Home .Each_Section .ResearchImage_right img {
    object-fit: contain;
    width: 400px;
    height: 400px;
}

.Home .Each_Section .ResearchText,
.Home .Each_Section .ResearchText_right {
    flex: 7;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    padding: 5rem;
    z-index: 1;
}

.Home .Each_Section .ResearchText p,
.Home .Each_Section .ResearchText_right p {
    margin-top: 0.5rem;
    text-align: justify;
    text-justify: inter-word;
    line-height: 1.5;
}

.Home .HomeButtons {
    justify-content: center;
    display: flex;
}

/* Third Section */

.Home .third_section {
    display: flex;
    justify-content: space-between;
    gap: 10rem;
}

.meet_our_team,
.publications {
    flex: 1;
    /* Make them take equal widths */
    display: flex;
    /* Make these flex containers too */
    flex-direction: column;
    /* Stack children vertically */
}

.meet_our_team_text,
.publications_text {
    flex-grow: 1;
    /* This will ensure the text container takes up all available space, pushing the button to the bottom */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* This will ensure maximum space between the text and the button */
}


.Home .meet_our_team_image,
.Home .publications_image {
    color: rgb(45, 99, 83);
    font-size: 125px;
    text-align: center;
    margin-bottom: 3.3vmax;
    display: flex;
    justify-content: center;
}

.Home .meet_our_team_text,
.Home .publications_text {
    margin: auto;
    z-index: 2;
    padding: 7%;
    box-shadow: -1px 1px 20px 9px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: white;
    color: black;
}


.Home .meet_our_team h2,
.Home .publications_text h2 {
    text-align: center;
    font-size: 2rem;
    padding-bottom: 0.5rem;
}

.Home .meet_our_team #meet_our_team_text_left,
.Home .publications_text #publications_text_right {
    text-align: center;
    text-justify: inter-word;
    line-height: 1.5;
    font-size: larger;
    padding-bottom: 1rem;
}

/* Fourth Section */
.Home .fourth_section_title {
    font-size: 2rem;
    padding-bottom: 3.3vmax;
    text-align: center;
}

.Home #fourth_section_button {
    margin-top: 2rem;
}

/* Fifth Section */

.Home .fifth_section_Title {
    text-align: center;
}

.Home .fifth_section_Title h2 {
    font-size: 2rem;
    padding-bottom: 3.3vmax;
}

.Home .fifth_section {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.Home .fifth_section_texts {
    display: flex;
    justify-content: space-between;
    gap: 10rem;
}

.Home .fifth_section_text_left,
.Home .fifth_section_text_right {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    z-index: 2;
    padding: 3.5%;
    box-shadow: -1px 1px 20px 9px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: white;
    color: black;
}

.Home .fifth_section_text_left #text_left,
.Home .fifth_section_text_right #text_right {
    text-align: center;
    text-justify: inter-word;
    line-height: 1.5;
    font-size: larger;
    padding-bottom: 1rem;
}

